import React from 'react';
import { Card, CardContent, Grid } from '@material-ui/core';
import { EHighlightFields, EntityHighlightsProps } from '../util/types';
import { GitInfoCard } from './EntityGitInfo';
import { DefaultGitInfoFields } from '../util';
import { HighlightsCustomFieldsCards } from './HighlightsCustomFieldsCards';

export const HighlightsCard = (props: EntityHighlightsProps) => {
    const builtInFields: EHighlightFields[] = [];
    if (props.fields) {
        builtInFields.push(...props.fields);
    } else {
        builtInFields.push(...DefaultGitInfoFields)
    }
    return (
        <Card>
            <CardContent>
                <Grid container spacing={3}>
                    {builtInFields.length > 0 && <GitInfoCard fields={builtInFields}/>}
                    {props.customFields !== undefined && <HighlightsCustomFieldsCards customFields={props.customFields}/>}
                </Grid>
            </CardContent>
        </Card>
    );
}

