import React  from 'react';
import { GitCommit } from '../api/types';
import { Link } from '@backstage/core-components';
import { Typography } from '@material-ui/core';
import LaunchIcon from '@material-ui/icons/LaunchOutlined';

export const GtInfoAuthorField = ({commits} : {commits: GitCommit[]}) => {
    return commits[0].authorHtmlUrl ? 
        <>
            <Link to={commits[0].authorHtmlUrl}>
                <>
                {commits[0].author}
                <LaunchIcon style={{fontSize: '16px'}}/>
                </>
            </Link>
        </> :
        <Typography variant='body2'>{commits[0].author}</Typography>
};